* {
  box-sizing: border-box;
}

.header-form {
  background-color: #4d5ae5;
  width: 100%;
  height: 100px;
  margin: 0;
  padding: 0;
}

.search-form {
  justify-content: center;
  gap: 10px;
  padding-top: 25px;
  display: flex;
}

.input {
  background: #fff;
  border: 1px solid #fff;
  border-radius: 4px;
  width: 396px;
  height: 30px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.btn {
  background: #fff;
  border: 1px solid #fff;
  border-radius: 4px;
  width: 156px;
  height: 35px;
}

.photo-card {
  background-color: #fff;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin-top: 20px;
  display: flex;
}

.photo-card {
  background-color: #bdbdbd;
  width: 300px;
  height: 300px;
  margin: 0 auto;
  transition: box-shadow .25s cubic-bezier(.4, 0, .2, 1);
  position: relative;
  overflow: hidden;
}

.img {
  width: 280px;
  height: 190px;
}

.load-btn {
  color: #fff;
  background: #4d5ae5;
  border: 1px solid #fff;
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  width: 156px;
  height: 35px;
  margin: 0 auto;
  display: flex;
}

.info {
  background-color: #3f51b5;
  width: 100%;
  height: 100%;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 0;
  left: 0;
  transform: translateY(100%);
}

.info-item {
  color: #fff;
  margin: 0;
  padding: 10px;
  font-size: 18px;
}

.photo-card:hover .overlay {
  transform: translateX(0);
}

.info-item:hover {
  box-shadow: 0 1px 6px rgba(46, 47, 66, .08), 0 1px 1px rgba(46, 47, 66, .16), 0 2px 1px rgba(46, 47, 66, .08);
}

